* {
  margin: 0;
  padding: 0;
}
header {
  height: 80px;
  background-color: navy;
  color: #fff;
  font-size: 2rem;
  line-height: 80px;
  font-weight: 900;
  padding-left: 1rem;
}
.box {
  border-bottom: 1px solid lightslategray;
  padding: 1rem;
  position: relative;
}
.good {
  position: absolute;
  right: 5%;
  top: 35%;
  font-weight: 900;
  cursor: pointer;
}
.good span {
  display: inline-block;
  width: 30px;
  text-align: center;
}
button {
  width: 100px;
  height: 40px;
  border: 0;
  margin-top: 20px;
  margin-left: 1rem;
  cursor: pointer;
}
.modal {
  width: 300px;
  height: 400px;
  border-radius: 50px;
  background-color: #fff;
  box-shadow: 0 0 10px #00000030;
  padding: 3rem;
  position: fixed;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.modal h4 {
  margin-bottom: 1rem;
  font-size: 2rem;
  text-transform: capitalize;
}
.modal p:nth-of-type(1) {
  margin-bottom: auto;
}